.accordionjs {
	position: relative;
	margin: 0;
	padding: 0;
	list-style: none;
	margin-top: 10px;
	margin-bottom: 20px;
  
	.acc_section {
	  border: 1px solid #eff4fc;
	  position: relative;
	  z-index: 10;
	  margin-top: -1px;
	  overflow: hidden;
  
	  .acc_head {
		position: relative;
		background: #fff;
		padding: 10px;
		display: block;
		cursor: pointer;
  
		h3 {
		  line-height: 1;
		  margin: 5px;
		  font-size: 16px;
		  padding-left: 25px;
		}
	  }
  
	  .acc_content {
		padding: 10px;
	  }
  
	  &:first-of-type {
		border-top-left-radius: 7px;
		border-top-right-radius: 7px;
  
		.acc_head {
		  border-top-left-radius: 7px;
		  border-top-right-radius: 7px;
		}
	  }
  
	  &:last-of-type {
		border-bottom-left-radius: 7px;
		border-bottom-right-radius: 7px;
  
		.acc_content {
		  border-bottom-left-radius: 7px;
		  border-bottom-right-radius: 7px;
		}
	  }
  
	  &.acc_active {
		> {
		  .acc_content {
			display: block;
		  }
  
		  .acc_head {
			border-bottom: 1px solid #eff4fc;
			color: #fff;
		  }
		}
  
		.acc_head h3:before {
		  content: "\e995";
		  position: absolute;
		  font-family: feather !important;
		  left: 10px;
		  color: #fff;
		  top: 13px;
		  font-size: 20px;
		  transition: all .5s;
		  transform: scale(1);
		}
	  }
  
	  .acc_head h3:before {
		content: "\e9b0";
		position: absolute;
		font-family: feather !important;
		left: 10px;
		top: 13px;
		font-size: 20px;
		transition: all .5s;
		transform: scale(1);
	  }
	}
  }