 *
 */

.flexdatalist-results {
    position: absolute;
    top: 0;
    left: 0;
    border-top: none;
    background: #fff;
    border-radius: 7px !important;
    z-index: 99;
    max-height: 300px;
    overflow-y: auto;
    box-shadow: 0 4px 5px rgb(108 106 169 / 38%);
    list-style: none;
    margin: 0;
    padding: 0;
    margin-top: 5px;

    li {
        border-bottom: 0px solid #efecfb;
        padding: 8px 15px;
        font-size: 14px;
        line-height: 20px;
        cursor: pointer;

        &.active {
            background-color: #ececf3;
        }

        span.highlight {
            font-weight: 700;
            text-decoration: none;
            background: none;
            border: 0;
        }

        &.no-results {
            font-style: italic;
        }

        &.group {
            background: #F3F3F4;
            color: #666;
            padding: 8px 8px;
        }

        .group-name {
            font-weight: 700;
        }

        .group-item-count {
            font-size: 85%;
            color: #777;
            display: inline-block;
            padding-left: 10px;
        }
    }
}

