/**
   * Multiple items
   */

.flexdatalist-multiple {
    &:before {
        content: '';
        display: block;
        clear: both;
    }

    width: 100%;
    margin: 0;
    padding: 0;
    list-style: none;
    text-align: left;
    cursor: text;

    &.disabled {
        background-color: #eee;
        cursor: default;
    }

    &:after {
        content: '';
        display: block;
        clear: both;
    }

    li {
        display: inline-block;
        position: relative;
        margin: 5px;
        float: left;

        &.input-container {
            border: none;
            height: auto;
            padding: 0 0 0 4px;
            line-height: 24px;

            input {
                border: none;
                height: auto;
                padding: 0 0 0 4px;
                line-height: 24px;
            }
        }

        &.value {
            display: inline-block;
            padding: 2px 25px 2px 7px;
            background: #eee;
            border-radius: 3px;
            line-height: 20px;
        }

        &.toggle {
            cursor: pointer;
            transition: opacity ease-in-out 300ms;

            &.disabled {
                text-decoration: line-through;
                opacity: 0.80;
            }
        }

        &.value span.fdl-remove {
            font-weight: 700;
            padding: 2px 5px;
            font-size: 20px;
            line-height: 20px;
            cursor: pointer;
            position: absolute;
            top: 0;
            right: 0;
            opacity: 0.70;

            &:hover {
                opacity: 1;
            }
        }
    }
}